/* SITE */
/*******/
body {
  font-family: 'daxline-pro', sans-serif;
  padding: 0px;
  margin: 0px; }

.icon {
  display: inline-block; }

.clear {
  clear: both; }

a:hover {
  text-decoration: underline;
  cursor: pointer; }

div.body {
  position: relative;
  overflow: hidden; }

/***********/
/* HEADER */
/*********/
.nav-bar {
  height: 70px;
  width: 100%;
  background: linear-gradient(270deg, #F2F4F2 0%, #FFFFFF 100%);
  box-shadow: 0 1px 0 0 #D9DEDA; }
  .nav-bar img.logo {
    margin-top: 14px;
    margin-left: 21px;
    width: 128px; }
  .nav-bar .join-button {
    margin-top: 17px;
    margin-right: 21px;
    color: #FFFFFF;
    float: right;
    text-align: center;
    font-size: 14px;
    font-weight: 900;
    line-height: 31px;
    height: 32px;
    width: 85px;
    border-radius: 2px;
    background: linear-gradient(90deg, #A5CD28 0%, #69A507 100%);
    box-shadow: 0 4px 7px 0 rgba(1, 36, 6, 0.2); }

/*****************/
/* TEXT BANNERS */
/***************/
.text-title-box {
  text-align: center;
  text-transform: uppercase;
  margin: 17px 0 34px 0; }
  .text-title-box h1 {
    color: #69A507;
    font-size: 28px;
    font-weight: 900;
    line-height: 30px;
    margin-bottom: 7px; }
    .text-title-box h1 span.black {
      color: #000000; }
  .text-title-box img {
    width: 120px; }

.text-info-banner {
  margin-top: 17px;
  margin-bottom: 68px;
  text-align: center;
  padding-top: 7px;
  padding-bottom: 7px;
  width: 100%;
  height: 70px;
  background-color: rgba(242, 244, 242, 0.7); }
  .text-info-banner img {
    width: auto;
    height: 70px;
    margin-right: 14px; }
  .text-info-banner div.textbox {
    height: 70px;
    display: inline-block;
    vertical-align: middle; }
    .text-info-banner div.textbox p {
      line-height: 10px;
      margin: 0px; }
      .text-info-banner div.textbox p span.bold {
        font-weight: bold; }
      .text-info-banner div.textbox p span.green {
        color: #69A507;
        font-weight: bold;
        margin-left: 20px; }

/**************************/
/* LOTTO CARD COMPONENTS */
/************************/
.lotto-card.two-thirds {
  width: calc(66.68% - 17px*1 - 7px*3); }
  .lotto-card.two-thirds div.lotto-numbers {
    float: left;
    width: calc(50% - 14px*2); }

.lotto-card.kenoland {
  width: calc(33.33% - 17px - (7px));
  margin-left: 17px;
  border: none;
  min-height: 302px; }
  .lotto-card.kenoland .header {
    background: #536519 url("/static/images/kenoland.png") no-repeat right top;
    background-size: auto 100%;
    height: 120px;
    width: 100%; }
  .lotto-card.kenoland .body {
    text-align: center;
    height: 180px;
    background: linear-gradient(90deg, #F56632 0%, #C62C82 58.64%, #C52B83 100%); }
    .lotto-card.kenoland .body p {
      margin: 0px;
      padding-top: 14px;
      font-weight: 500;
      font-size: 14px;
      color: #FFFFFF; }
    .lotto-card.kenoland .body img {
      height: 57px;
      width: auto;
      margin-top: 7px;
      margin-right: 7px; }
    .lotto-card.kenoland .body div.floatLeft {
      height: 70px;
      display: inline-block;
      vertical-align: middle; }
      .lotto-card.kenoland .body div.floatLeft p {
        font-weight: 900;
        line-height: 0px;
        font-size: 20px; }
        .lotto-card.kenoland .body div.floatLeft p span.large {
          font-size: 40px; }

.lotto-card {
  position: relative;
  float: left;
  width: calc(33.33% - 17px - (7px*2));
  margin-left: 24px;
  margin-bottom: 17px;
  min-height: 300px;
  border: 1px solid #D9DEDA; }
  .lotto-card div.borderLeft {
    position: absolute;
    height: 100%;
    width: 7px;
    background-color: #714C9C;
    left: -7px; }
  .lotto-card div.img {
    overflow: hidden;
    position: relative;
    width: 100%;
    height: 154px;
    color: #FFFFFF;
    background-size: 100%; }
    .lotto-card div.img div.head-text {
      margin: 0px;
      padding: 14px; }
      .lotto-card div.img div.head-text p {
        margin: 0px; }
    .lotto-card div.img p.title {
      color: #FFFFFF;
      font-size: 28px;
      font-weight: 900;
      line-height: 30px;
      text-shadow: 0 0 21px 0 #000000; }
    .lotto-card div.img p.sub-title {
      font-size: 14px;
      font-weight: 500;
      line-height: 16px; }
    .lotto-card div.img div.rectangle {
      position: absolute;
      text-align: center;
      bottom: 0px;
      height: 50px;
      width: 100%;
      background-color: rgba(0, 0, 0, 0.4);
      box-shadow: 2px 2px 7px 0 rgba(1, 36, 6, 0.28); }
    .lotto-card div.img div.inner-text {
      position: relative;
      top: 7px;
      height: 100%;
      font-size: 14px;
      display: inline-block;
      line-height: 20px; }
      .lotto-card div.img div.inner-text .title {
        font-weight: 900;
        text-transform: uppercase; }
    .lotto-card div.img .icon.selection-left {
      height: 100%;
      margin-right: 20%;
      font-size: 21px; }
    .lotto-card div.img .icon.selection-right {
      height: 100%;
      margin-left: 20%;
      font-size: 21px; }
  .lotto-card div.lotto-numbers {
    float: left;
    width: 100%;
    padding: 14px; }
    .lotto-card div.lotto-numbers p.title {
      margin: 0px;
      margin-bottom: 7px; }
    .lotto-card div.lotto-numbers div.number-circle {
      float: left;
      margin-right: 7px;
      margin-bottom: 7px;
      border: 1px solid #D9DEDA;
      border-radius: 50%;
      height: 35px;
      width: 35px;
      line-height: 37px;
      text-align: center; }
    .lotto-card div.lotto-numbers div.number-circle.red {
      border: 1px solid #C53422; }
  .lotto-card div.card-links {
    position: relative;
    clear: both;
    padding-top: 7px;
    text-align: right;
    width: 100%;
    color: #aaa;
    font-size: 22px;
    margin-bottom: 7px; }
    .lotto-card div.card-links a {
      color: #69A507;
      font-size: 14px;
      font-weight: bold;
      margin: 0 14px 0 14px; }

